px #ef00e3a8, 0 0 20px #ef00e3a8, 0 0 2px #fed128, 2px 2px 2px #ddb00e;\r\n    color: #f5efcb;\r\n  }\r\n\r\n  50% {\r\n    text-shadow: 0 0 2px #800e0b, 0 0 5px #800e0b, 0 0 5px #800e0b, 0 0 5px #7f98c5, 0 0 2px #800e0b, 4px 4px 2px #40340a;\r\n    color: #e9e3e7;\r\n  }\r\n}\r\n\r\ni {\r\n  cursor: pointer;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n}\r\n\r\nimg {\r\n  width: 100%;\r\n}\r\n\r\n.like-icon {\r\n  width: 20px;\r\n  cursor: pointer;\r\n}\r\n\r\nbutton {\r\n  padding: 10px 15px;\r\n  font-size: 16px;\r\n  color: #fff;\r\n  cursor: pointer;\r\n  background: rgba(33, 68, 246, 0.9);\r\n  border: none;\r\n  border-radius: 15px;\r\n}\r\n\r\nbutton:hover {\r\n  transform: scale(0.9);\r\n}\r\n\r\n.msg {\r\n  display: none;\r\n  position: fixed;\r\n  left: 70%;\r\n  width: 200px;\r\n  padding: 10px 10px;\r\n  border-radius: 15px;\r\n  text-align: center;\r\n  color: #fff;\r\n  z-index: 10;\r\n}\r\n\r\n.container {\r\n  padding: 0 40px;\r\n  max-width: 1440px;\r\n  margin: 0 auto;\r\n}\r\n\r\n/* header */\r\n.head {\r\n  background: rgba(105, 129, 248, 0.8);\r\n}\r\n\r\n.navbar {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  height: 50px;\r\n}\r\n\r\n.navbar .navbar-ul {\r\n  list-style-type: none;\r\n  display: flex;\r\n  gap: 20px;\r\n}\r\n\r\n.navbar .navbar-ul .navbar-link {\r\n  color: #fff;\r\n  font-size: 15px;\r\n  font-weight: 600px;\r\n}\r\n\r\n.movies {\r\n  margin: 50px 0;\r\n}\r\n\r\n.movies .container {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  gap: 20px;\r\n}\r\n\r\n.movies .card {\r\n  max-width: 400px;\r\n  background: #efefef;\r\n  padding: 10px;\r\n  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);\r\n  border-radius: 20px;\r\n}\r\n\r\n.movies .card h3 {\r\n  margin-block: 10px;\r\n}\r\n\r\n.movies img {\r\n  border-radius: 20px;\r\n}\r\n\r\n.movies .card .card-body {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-block: 20px;\r\n}\r\n\r\n/* Footer  */\r\n.foot {\r\n  background-color: rgba(105, 129, 248, 0.8);\r\n}\r\n\r\n.foot .container {\r\n  height: 50px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  color: white;\r\n}\r\n\r\n.popContainer {\r\n  position: fixed;\r\n  display: none;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  z-index: 1;\r\n  padding: 60px 0;\r\n  overflow: auto;\r\n  background: rgba(36, 36, 36, 0.9);\r\n}\r\n\r\n.pop {\r\n  max-width: 600px;\r\n  padding: 20px;\r\n  margin: 0 auto;\r\n  background: #fff;\r\n  border-radius: 20px;\r\n}\r\n\r\n.popup {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.comment-count {\r\n  display: flex;\r\n  gap: 20px;\r\n  margin-top: 10px;\r\n  margin-bottom: -10px;\r\n}\r\n\r\n.pop .title {\r\n  margin-block: 10px;\r\n  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;\r\n}\r\n\r\n.pop .summary {\r\n  margin-block: 10px;\r\n  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;\r\n}\r\n\r\n.commentDiv {\r\n  height: 200px;\r\n  overflow-y: auto;\r\n  margin-block: 20px;\r\n}\r\n\r\n.commentDiv p {\r\n  background: rgba(36, 36, 36, 0.9);\r\n  color: #fff;\r\n  padding: 10px 20px;\r\n  margin-block: 10px;\r\n  border-radius: 20px;\r\n}\r\n\r\n.nameField,\r\n.commentField {\r\n  width: 100%;\r\n  border-radius: 20px;\r\n  padding: 5px 15px;\r\n  color: #242424;\r\n  font-size: 14px;\r\n  border: 2px solid #242424e6;\r\n}\r\n\r\n.nameField::placeholder,\r\n.commentField::placeholder {\r\n  color: #242424;\r\n}\r\n\r\n.closeBtn {\r\n  width: 20px;\r\n  margin-bottom: 10px;\r\n  position: relative;\r\n  left: 97%;\r\n}\r\n\r\n.addComment {\r\n  display: inline-block;\r\n  width: 200px;\r\n  height: 40px;\r\n  color: #fff;\r\n  margin-top: 10px;\r\n  background: rgba(36, 36, 36, 0.9);\r\n  border-radius: 20px;\r\n}\r\n\r\nform {\r\n  